import { useState } from "react"

import { Typography } from "@mui/material"
import { statusIcons } from "../../typings/Statuses"
import StatusHud from "./StatusHud"
import HealthHud from "./HealthHud"
import ScaleFade from "../../transitions/ScaleFade"
import { useNuiEvent } from "../../hooks/useNuiEvent"
import MoneyHud from "./MoneyHud"
function PlayerHud() {
  const [playerHudStatus, setPlayerHudStatus] = useState<any>(statusIcons)
  const [streetAndZone, setStreetAndZone] = useState<any>({
    zone: "Los Santos International Airport",
    street: "Exceptionalists Way",
  })

  const statusesHud = playerHudStatus.filter(
    (status: any) => status.id !== "health" && status.id !== "armour"
  )
  const healthHud = playerHudStatus.filter(
    (status: any) => status.id === "health" || status.id === "armour"
  )

  useNuiEvent("updateHudStatus", (data) => {
    setPlayerHudStatus((prev: any) =>
      prev.map((status: any) => {
        switch (status.id) {
          case "health":
            return { ...status, value: data.health }
          case "armour":
            return { ...status, value: data.armor }
          case "stamina":
            return { ...status, value: data.stamina }
          case "stress":
            return { ...status, value: data.stress }
          case "hunger":
            return { ...status, value: data.hunger }
          case "thirst":
            return { ...status, value: data.thirst }
          default:
            return status
        }
      })
    )
  })

  useNuiEvent("updateHudInfo", (data) => {
    setStreetAndZone({ ...streetAndZone, street: data.street, zone: data.zone })
  })

  return (
    <div
      className="flex flex-col justify-between "
      style={{ height: "18.5vh" }}
    >
      <div className="status-hud flex flex-row gap-2">
        {statusesHud.map((hud: any) => (
          <ScaleFade
            visible={hud.value !== 0 && hud.value !== 100}
            key={hud.id}
          >
            <StatusHud hud={hud} key={hud.id} />
          </ScaleFade>
        ))}
      </div>
      <div className="flex flex-col gap-2">
        {healthHud.map((hud: any) => (
          <ScaleFade visible={hud.visible} key={hud.id} from="left">
            <HealthHud hud={hud} key={hud.id} />
          </ScaleFade>
        ))}
      </div>
      <div className="navigations flex flex-col justify-start items-start drop-shadow">
        <Typography variant="body1" className="drop-shadow" fontWeight={500}>
          {streetAndZone.zone}
        </Typography>
        <Typography
          variant="body2"
          className="drop-shadow"
          sx={{ color: "text.secondary" }}
        >
          {streetAndZone.street}
        </Typography>
      </div>
      <div className="player-money self-start flex flex-col justify-start drop-shadow gap-0.5">
        <MoneyHud />
      </div>
    </div>
  )
}

export default PlayerHud
