import { HeadsetMic, Mic, MicNone } from "@mui/icons-material"
import React, { useState } from "react"
import { useNuiEvent } from "../../hooks/useNuiEvent"
import {  Typography } from "@mui/material"

function VoiceHud() {
  const [voiceToggle, setVoiceToggle] = useState(false)
  const [radioChannel, setRadioChannel] = useState<number>(0)

  useNuiEvent("updateHudStatus", (data) => {
    setVoiceToggle(data.voice) //boolean
    setRadioChannel(data.radio) // number
  })

  return (
    <>
      {voiceToggle ? (
        radioChannel !== 0 ? (
          <div className="flex flex-col items-center ">
            <HeadsetMic
              className="drop-shadow"
              sx={{
                color: "text.primary",
                fontSize: 25,
                top: 0,
              }}
            />
            <Typography variant="caption" color="text.primary">
              {radioChannel}
            </Typography>
          </div>
        ) : (
          <Mic
            className="drop-shadow"
            sx={{
              color: "text.primary",
              fontSize: 25,
              top: 0,
            }}
          />
        )
      ) : radioChannel !== 0 ? (
        <div className="flex flex-col items-center ">
          <HeadsetMic
            className="drop-shadow"
            sx={{
              color: "text.secondary",
              fontSize: 25,
              top: 0,
            }}
          />
          <Typography variant="caption" color="text.secondary">
            {radioChannel}
          </Typography>
        </div>
      ) : (
        <>
          <MicNone
            className="drop-shadow"
            sx={{
              color: (theme) => theme.palette.grey[400],
              fontSize: 25,
              top: 0,
            }}
          />
        </>
      )}
    </>
  )
}

export default VoiceHud
