import React from "react"
import { AnimatePresence, motion } from "framer-motion"

const ScaleFade: React.FC<{
  from?: "left" | "right"
  initialX?: number
  visible: boolean
  children: React.ReactNode
  onExitComplete?: () => void
  willScale?: boolean
  delay?: number
  key?: any
}> = ({
  visible,
  children,
  onExitComplete,
  from,
  initialX = 0,
  willScale,
  delay,
  key,
}) => {
  if (from) {
    initialX = from === "right" ? 200 : -200
  }

  return (
    <AnimatePresence>
      {visible && (
        <motion.div
          key={key}
          initial={{ x: initialX, opacity: 0, scale: willScale ? 0.95 : 1 }}
          animate={{
            x: 0,
            opacity: 1,
            scale: 1,
            transition: { duration: delay || 0.2, ease: [0, 0, 0.5, 1] },
          }}
          exit={{
            x: initialX,
            opacity: 0,
            scale: 0.95,
            transition: { duration: delay || 0.2, ease: [0.4, 0, 1, 1] },
          }}
        >
          {children}
        </motion.div>
      )}
    </AnimatePresence>
  )
}

export default ScaleFade
