import { Avatar, Typography } from "@mui/material"
import { blue } from "@mui/material/colors"
import React from "react"

type Props = {}

function CoinMark({}: Props) {
  return (
    <Avatar
      className="leading-0 tracking-tighter"
      sx={{ width: 18, height: 18, bgcolor: blue[600] }}
    >
      <Typography
        className="pr-0.5 italic font-extrabold"
        style={{
          fontFamily: "Lobster Two, sans serif",
          fontSize: ".75rem",
          textAlign: "center",
          color: "white",
        }}
      >
        T
      </Typography>
    </Avatar>
  )
}

export default CoinMark
