import React, { useState } from "react"

import { CssBaseline, ThemeProvider } from "@mui/material"
import { theme } from "../theme/theme"
import ServerInfo from "./Top/ServerInfo"

import PlayerHud from "./Bottom/PlayerHud"
import ScaleFade from "../transitions/ScaleFade"

import VehicleHud from "./Bottom/VehicleHud"
import { useNuiEvent } from "../hooks/useNuiEvent"
import VoiceHud from "./Bottom/VoiceHud"
import { isEnvBrowser } from "../utils/misc"
const App: React.FC = () => {
  const [visible, setVisible] = useState(isEnvBrowser())
  useNuiEvent("toggleUi", (data) => {
    setVisible(data.open)
  })
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <ScaleFade visible={visible} delay={0.1}>
        <div className="App w-screen h-screen relative flex flex-col justify-between p-4">
          <div className="top-section flex flex-row justify-end w-full">
            <ServerInfo />
          </div>

          <div className="mapborder">
            <div className="square">
              <div className="top-of-map absolute bottom-0.5 right-1 flex flex-row self-start">
                <VoiceHud />
              </div>
            </div>
          </div>
          <div className="flex flex-row justify-between ">
            <div className="responsive player-hud">
              <ScaleFade key="playerHud" visible={visible} from="left">
                <PlayerHud />
              </ScaleFade>
            </div>
            <div className="odometer self-end ">
              <VehicleHud />
            </div>
          </div>
        </div>
      </ScaleFade>
    </ThemeProvider>
  )
}

export default App
