import { StatIconProps } from "../../typings/Statuses"
import { LinearProgress, linearProgressClasses } from "@mui/material"

interface HealthHudProps {
  hud: StatIconProps
}

function HealthHud({ hud }: HealthHudProps) {
  // const [progress, setProgress] = React.useState(10)
  // useEffect(() => {
  //   const timer = setInterval(() => {
  //     setProgress((prevProgress) =>
  //       prevProgress >= 100 ? 10 : prevProgress + 10
  //     )
  //   }, 800)
  //   return () => {
  //     clearInterval(timer)
  //   }
  // }, [])
  return (
    <div className="flex flex-row gap-1 items-center drop-shadow">
      <hud.icon style={{ color: hud.iconColor, fontSize: "20px" }} />
      <LinearProgress
        variant="determinate"
        className="drop-shadow"
        value={hud.value}
        sx={{
          height: 5,
          borderRadius: 5,
          width: hud.id === "health" ? 130 : 90,
          [`&.${linearProgressClasses.colorPrimary}`]: {
            backgroundColor: "rgba(30,30,30,0.95)",
          },
          [`& .${linearProgressClasses.bar}`]: {
            borderRadius: 5,
            backgroundColor: "#fff",
            animationDuration: "2s",
          },
        }}
      />
    </div>
  )
}

export default HealthHud
