import { CreditCard, Payments } from "@mui/icons-material"
import { Typography } from "@mui/material"
import React, { useState } from "react"
import CountUp from "react-countup"
import { useNuiEvent } from "../../hooks/useNuiEvent"
import { AnimatePresence, motion } from "framer-motion"

function MoneyHud() {
  const [balances, setBalances] = useState<any>({ bank: 0, cash: 0 })
  const [startBalances, setStartBalances] = useState<any>({ bank: 0, cash: 0 })
  const [isMinus, setIsMinus] = useState<any>({ bank: false, cash: false })
  const [activeMoneyChange, setActiveMoneyChange] = useState<any>({
    bank: false,
    cash: false,
  })
  useNuiEvent("toggleMoney", (data) => {
    setActiveMoneyChange({ bank: false, cash: false })
    setBalances({ bank: data.bank, cash: data.cash })
  })
  useNuiEvent("updateMoney", (data) => {
    setBalances({ bank: data.bank, cash: data.cash })
  })
  const handleMoneyChange = (data: any) => {
    setActiveMoneyChange((prev: any) => ({ ...prev, [data.type]: true }))
    setIsMinus((prev: any) => ({ ...prev, [data.type]: data.isMinus }))
    setStartBalances((prev: any) => ({
      ...prev,
      [data.type]: balances[data.type],
    }))
    setBalances((prev: any) => ({
      ...prev,
      [data.type]:
        prev[data.type] + (data.isMinus ? -data.amount : data.amount),
    }))
  }

  useNuiEvent("moneyChange", handleMoneyChange)

  const moneyDisplay = (type: string, start: number, end: number) => (
    <Typography
      variant="body2"
      fontWeight={600}
      className="drop-shadow tracking-wide inline-block"
      sx={{
        color: (theme) =>
          activeMoneyChange[type]
            ? isMinus[type]
              ? theme.palette.error.light
              : theme.palette.success.light
            : "text.primary",
      }}
    >
      <AnimatePresence>
        <motion.div initial={{ scale: 1 }} key={end}>
          <CountUp
            start={start}
            end={end}
            prefix="$"
            onStart={() =>
              setActiveMoneyChange((prev: any) => ({ ...prev, [type]: true }))
            }
            onEnd={() =>
              setActiveMoneyChange((prev: any) => ({ ...prev, [type]: false }))
            }
          />
        </motion.div>
      </AnimatePresence>
    </Typography>
  )

  return (
    <>
      <div className="online-players flex flex-row items-center gap-3 drop-shadow ">
        <Payments fontSize="small" />
        {moneyDisplay("cash", startBalances.cash, balances.cash)}
      </div>
      <div className="online-players flex flex-row items-center gap-3 drop-shadow ">
        <CreditCard fontSize="small" />
        {moneyDisplay("bank", startBalances.bank, balances.bank)}
      </div>
    </>
  )
}

export default MoneyHud
