import { useEffect, useState } from "react"
import {
  CarCrash,
  LocalGasStation,
  Person,
  PersonOff,
  Speed,
} from "@mui/icons-material"
import { Divider, Typography } from "@mui/material"
import ScaleFade from "../../transitions/ScaleFade"
import { AnimatePresence, motion } from "framer-motion"
import { useNuiEvent } from "../../hooks/useNuiEvent"
import { isEnvBrowser } from "../../utils/misc"
function VehicleHud() {
  const [hudVisible, setHudVisible] = useState(isEnvBrowser())
  const [vehicleHudState, setVehicleHudState] = useState({
    speed: 34,
    engine: 30,
    fuel: 5,
    cGear: 3,
    seatbelt: false,
    cruise: false,
  })
  const { cGear, engine, fuel, cruise, seatbelt, speed } = vehicleHudState
  const speedStr = speed.toString().padStart(3, "0")
  const firstNonZeroIndex = speedStr.split("").findIndex((char) => char !== "0")
  const [gearDirection, setGearDirection] = useState("up")

  useNuiEvent("hudVehicle", (data) => {
    setHudVisible(data.open)
  })
  useNuiEvent("updateHudVehicle", (data) => {
    const { cGear } = data

    if (cGear - vehicleHudState.cGear > 0) {
      setGearDirection("up")
    } else if (cGear - vehicleHudState.cGear < 0) {
      setGearDirection("down")
    }
    setVehicleHudState((prev) => ({ ...prev, ...data }))
  })

  useEffect(() => {}, [gearDirection])
  useNuiEvent("toggleSeatbelt", (data) => {
    setVehicleHudState((prev) => ({ ...prev, seatbelt: data.active }))
  })
  useNuiEvent("toggleCruise", (data) => {
    setVehicleHudState((prev) => ({ ...prev, cruise: data.active }))
  })
  return (
    <ScaleFade visible={hudVisible} key={"VehicleHud"}>
      <div className="odometer absolute right-8 bottom-8 flex flex-col justify-end items-end drop-shadow gap-1">
        <Typography
          variant="h6"
          fontWeight={600}
          component={"div"}
          lineHeight={0}
          className="drop-shadow"
        >
          MP/H
        </Typography>
        <div className="flex flex-row gap-1 justify-center items-center mt-4 ">
          <div className="gear text-black bg-white w-7 h-9 rounded-sm flex items-center justify-center overflow-hidden">
            <AnimatePresence mode="wait">
              <motion.div
                key={cGear}
                initial={{
                  y: gearDirection === "up" ? -30 : 30,
                  opacity: 0.5,
                }}
                animate={{
                  y: 0,
                  opacity: 1,
                }}
                exit={{
                  y: gearDirection === "up" ? 30 : -30,
                  opacity: 0.5,
                }}
              >
                <Typography variant="h5" fontWeight={600}>
                  {cGear === 0 ? "N" : cGear === -1 ? "R" : cGear}
                </Typography>
              </motion.div>
            </AnimatePresence>
          </div>
          <div className="flex items-center">
            {speedStr.split("").map((char, index) => (
              <Typography
                key={index}
                component={"span"}
                variant="h2"
                fontWeight={600}
                lineHeight={0}
                className="drop-shadow"
                sx={{
                  width: "40px", // Set a fixed width appropriate for your design
                  display: "inline-block",
                  textAlign: "center",
                  transition: "ease 0.01",
                  color: (theme) =>
                    (char === "0" && index < firstNonZeroIndex) ||
                    firstNonZeroIndex === -1
                      ? "text.secondary"
                      : "text.primary",
                }}
              >
                {char}
              </Typography>
            ))}
          </div>
        </div>
        <div className="flex flex-row gap-2 mt-1 items-center justify-center">
          <CarCrash
            className="engine drop-shadow"
            sx={{
              color: (theme) =>
                engine <= 15
                  ? theme.palette.error.main
                  : engine <= 45
                  ? theme.palette.warning.main
                  : theme.palette.grey[400],
              fontSize: 22,
            }}
          />
          {seatbelt ? (
            <Person
              className="seatbelt drop-shadow"
              sx={{
                color: (theme) => theme.palette.grey[400],
                fontSize: 22,
              }}
            />
          ) : (
            <PersonOff
              className="seatbelt drop-shadow"
              sx={{
                color: (theme) => theme.palette.error.main,
                fontSize: 22,
              }}
            />
          )}

          <Speed
            className="drop-shadow"
            sx={{
              color: (theme) =>
                !cruise ? theme.palette.error.main : theme.palette.grey[400],
              fontSize: 22,
            }}
          />
          <Divider
            orientation="vertical"
            variant="fullWidth"
            className="bg-neutral-50 w-0.5"
            light={true}
            style={{ height: "12px" }}
          />
          <LocalGasStation
            className="drop-shadow "
            sx={{
              fontSize: 18,
              marginRight: -0.5,
              color: (theme) =>
                fuel <= 15
                  ? theme.palette.error.main
                  : fuel <= 50
                  ? theme.palette.warning.main
                  : "text.primary",
            }}
          />
          <div className="">
            <Typography
              component={"span"}
              className="tracking-wide"
              variant="body1"
            >
              {fuel}
            </Typography>
            <Typography
              variant="body2"
              component={"span"}
              className="drop-shadow"
              sx={{ color: "text.secondary" }}
            >
              %
            </Typography>
          </div>
        </div>
      </div>
    </ScaleFade>
  )
}

export default VehicleHud
