import { Paper, Typography } from "@mui/material"
import { useNuiEvent } from "../../hooks/useNuiEvent"
import { useEffect, useState } from "react"
import CoinMark from "./CoinMark"
import { isEnvBrowser } from "../../utils/misc"

function TimeOnline() {
  const [timeOnline, setTimeOnline] = useState(17999)
  const [showTimeOnline, setShowTimeOnline] = useState(false)
  const [hour, setHour] = useState(0)
  const [minute, setMinute] = useState(0)

  useEffect(() => {
    const interval = setInterval(() => {
      const newHour = Math.floor(timeOnline / 3600) // Using 3600 for hour calculation
      const newMinute = (timeOnline % 3600) / 60 // Corrected this line for minute calculation

      setHour(newHour)
      setMinute(Math.floor(newMinute)) // Round down the minute value

      setTimeOnline((prev) => (prev > 0 ? prev - 1 : 0))
    }, 1000)

    return () => clearInterval(interval)
  }, [timeOnline])

  useEffect(() => {
    if (isEnvBrowser()) {
      setShowTimeOnline(true)
    }
  }, [])

  useNuiEvent("showTimeOnline", (data) => {
    setShowTimeOnline(data.show)
    setTimeOnline(data.time)
  })

  return (
    <>
      {showTimeOnline && (
        <Paper className="flex flex-col gap-2 justify-start items-start text-left px-4 py-2 w-64">
          <div className="flex flex-row gap-2 items-center w-full justify-between">
            <div className="flex flex-row gap-2 items-center">
              <Typography variant="body2" fontWeight={500} component={"span"}>
                Nhn min ph 1
              </Typography>
              <CoinMark />
            </div>
            <div className=""></div>
          </div>
          <Typography
            variant="caption"
            color={"text.secondary"}
            fontWeight={500}
            component={"span"}
          >
            Thi gian:{" "}
            <Typography
              variant="body2"
              fontWeight={500}
              color={"text.primary"}
              component={"span"}
            >
              {hour < 1 ? `${minute} pht` : `${hour} gi ${minute} pht`}
            </Typography>
          </Typography>
        </Paper>
      )}
    </>
  )
}

export default TimeOnline
