import { Box } from "@mui/material"
import CircularProgress, {
  circularProgressClasses,
} from "@mui/material/CircularProgress"
import { StatIconProps } from "../../typings/Statuses"
import React from "react"

interface StatusHudProps {
  hud: StatIconProps
}
function StatusHud({ hud }: StatusHudProps) {
  return (
    <div
      style={{
        position: "relative",
      }}
    >
      <Box sx={{ position: "relative", display: "inline-flex" }}>
        <div className="flex relative">
          <CircularProgress
            variant="determinate"
            value={hud.value}
            size={40}
            thickness={4}
            sx={{
              color: hud.barColor,
              [`& .${circularProgressClasses.circle}`]: {
                boxShadow: "1px 1px #fff",
                strokeLinecap: "round",
              },
            }}
          />
        </div>

        <CircularProgress
          variant="determinate"
          value={100}
          size={40}
          thickness={4}
          sx={{
            position: "absolute",
            zIndex: -1,
            color: "rgba(30,30,30,0.95)",
            [`& .${circularProgressClasses.circle}`]: {
              strokeLinecap: "round",
              backgroundColor: "red",
            },
            [`& .${circularProgressClasses.svg}`]: {
              strokeLinecap: "round",
              backgroundColor: "rgba(30,30,30,0.95)",
              borderRadius: "50%",
            },
          }}
        />

        <Box
          sx={{
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            position: "absolute",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          {React.isValidElement(hud.icon) ? (
            React.cloneElement(hud.icon, {
              style: { color: hud.iconColor, fontSize: "20px" },
            } as React.Attributes)
          ) : (
            <hud.icon style={{ color: hud.iconColor, fontSize: "20px" }} />
          )}
        </Box>
      </Box>
    </div>
  )
}

export default StatusHud
