import { Avatar, Divider, Typography } from "@mui/material"
import { useNuiEvent } from "../../hooks/useNuiEvent"
import { useState } from "react"
import GunHud from "./GunHud"
import { blue } from "@mui/material/colors"
import TimeOnline from "./TimeOnline"

function ServerInfo() {
  const date = new Date()
  const time = date.toLocaleTimeString("en-us")
  const [membersOnline, setMembersOnline] = useState(100)

  const [playerInfo, setPlayerInfo] = useState({ id: 381, citizenId: 49267 })
  const [isDoubleEvent, setIsDoubleEvent] = useState(false)
  useNuiEvent("updateHudInfo", (data) => {
    setMembersOnline(data.mem)
    setPlayerInfo((prev) => ({
      ...prev,
      id: data.id,
      citizenId: data.citizenId,
    }))
  })
  useNuiEvent("showTimeOnline", (data) => {
    setIsDoubleEvent(data.double)
  })
  return (
    <div className="server-info text-right drop-shadow flex flex-col gap-2">
      <div className="logo flex flex-row gap-2 justify-end items-center">
        {isDoubleEvent && (
          <Avatar
            variant="square"
            className="shadow"
            sx={{
              bgcolor: blue[500],
              width: 22,
              height: 20,
              borderRadius: "2px",
            }}
          >
            <Typography
              variant="caption"
              color={"text.primary"}
              fontWeight={700}
              fontStyle={"italic"}
              className="drop-shadow"
            >
              x2
            </Typography>
          </Avatar>
        )}
        <Typography
          className="drop-shadow"
          variant="h4"
          fontWeight={700}
          fontStyle={"italic"}
        >
          Teddy
        </Typography>
      </div>
      <div className="server-info flex flex-row justify-end gap-3">
        <Typography variant="body1" className="drop-shadow" fontWeight={500}>
          {time}
        </Typography>
        <div className="online-players flex flex-row items-center gap-1 drop-shadow ">
          <Typography
            variant="body1"
            className="drop-shadow"
            sx={{ color: "text.secondary" }}
          >
            Online:{" "}
          </Typography>
          <Typography variant="body1" className="drop-shadow" fontWeight={500}>
            {membersOnline}
          </Typography>
        </div>
      </div>
      <div className="player-info  flex flex-row items-center justify-end gap-2 drop-shadow ">
        <div className="online-players flex flex-row items-center gap-1 drop-shadow ">
          <Typography
            variant="body1"
            className="drop-shadow"
            sx={{ color: "text.secondary" }}
          >
            ID: {playerInfo.id}
          </Typography>
        </div>
        <Divider
          orientation="vertical"
          variant="fullWidth"
          className="bg-neutral-50 w-0.5"
          light={true}
          style={{ height: "12px" }}
        />
        <div className="date flex items-center gap-1 w-max drop-shadow ">
          <Typography variant="body1" className="drop-shadow" fontWeight={500}>
            #{playerInfo.citizenId}
          </Typography>
        </div>
      </div>
      <div className="player-info flex flex-row items-center justify-end gap-1 drop-shadow ">
        <GunHud />
      </div>
      <TimeOnline />
    </div>
  )
}

export default ServerInfo
